.a {
  height: 100vh;
  padding: 30px 0px;
  display: flex;
  align-items: center;
  background-image: url("../img/sfondo.png");
  background-position: top center;
  background-size: 100% 120%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: #73a848;
}

.a-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}

.a-right {
  flex: 1;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 20px;
}

.a-card {
  width: 80%;
  height: 100%;
  /* border-radius: 10px; */
  position: relative;
  overflow: hidden;
}

.a-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.a-title {
  font-weight: 400;
  padding: 0px 0px 20px 0px;
}

.a-desc {
  font-weight: 300;
}

