.h {
  display: flex;
  justify-content: center;
  text-align: center;
  color: black;
  background-image: url("../img/sfondo.png");
  background-position: top center;
  background-size: 100% 120%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color: rgba(0, 0, 0, 0.4);
}

.h-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
  padding: 100px 0;
}

.chess {
  max-width: 200%; /* Ensure the image fits within its container */
  max-height: 200%; /* Ensure the image fits within its container */
}

.h-wrapper {
  width: 80%;
  display: flex;
  flex-direction: column; /* Stack children vertically */
  justify-content: center; /* Center vertically */
  align-items: center; /* Center horizontally */
  margin: 0 auto; /* Center the h-wrapper itself horizontally */
}

.h-top {
  padding: 40px 0px;
  display: flex;
}

.h-top-left {
  flex: 1;
  background-color: rgba(0, 0, 0, 0.4);
  padding: 20px;
  text-align: left;
}

.h-top-right {
  flex: 1;
  background-color: rgba(0, 0, 0, 0.4);
  padding: 20px;
  text-align: left;
}
